import React, {useEffect} from 'react';
import {Col, Row} from 'antd';
import styled from 'styled-components';
import PageHeader from '@iso/components/utility/pageHeader';
import Box from '@iso/components/utility/box';
import LayoutWrapper from '@iso/components/utility/layoutWrapper';
import ContentHolder from '@iso/components/utility/contentHolder';
import basicStyle from '@iso/assets/styles/constants';
import {Provider, useDispatch} from 'react-redux';
import {store} from '../../redux/store';
import LinceToolForm from "../../components/lince/LinceToolForm";
import {fetchSessionParameters} from "../../redux/lince/videoActions";
import IntlMessages from "@iso/components/utility/intlMessages";
import {IntlProvider} from "react-intl";
/*
GA code
 */
import siteConfig from '../../config/site.config';
import ReactGA from 'react-ga';

ReactGA.initialize(siteConfig.google.analyticsKey);
ReactGA.pageview(window.location.pathname + window.location.search);
/*
End GA code
 */
const StyledLayoutWrapper = styled(LayoutWrapper)`
  padding: 10px 10px !important;
`;
export default function () {
    const dispatch = useDispatch();
    useEffect(() => {
        dispatch(fetchSessionParameters());
    }, [dispatch]);
    const {rowStyle, colStyle, gutter} = basicStyle;
    return (
        <Provider store={store}>
            <IntlProvider>
                <StyledLayoutWrapper>
                    <PageHeader><IntlMessages id="tool-config"/></PageHeader>
                    <Row style={rowStyle} gutter={gutter} justify="start">
                        <Col md={24} sm={24} xs={24} style={colStyle}>
                            <Box title={<IntlMessages id="your-tool"/>}>
                                <ContentHolder>
                                    <LinceToolForm/>
                                </ContentHolder>
                            </Box>
                        </Col>
                    </Row>
                </StyledLayoutWrapper>
            </IntlProvider>
        </Provider>
    );
}
